import React from 'react';

import Edit from './Edit';
import Run from './Run';

import Table, { TableBody, TableCell, TableHead, TableRow } from 'material-ui/Table';


export default class Schedule extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: []
        };
        this.loadData = this.loadData.bind(this);
    }

    loadData() {
        if (!this.props.autoRefresh) {
            return;
        }
        // var result = [
        //     {id: 1, name: 'aaa', server_name: 'sadasd', status: 'stopped', last_start_at: '', next_start_at: '', schedule_type: 'interval', schedule_value: '1 minutes'},
        //     {id: 2, name: 'aaa2', server_name: 'sadasd2', status: 'stopped', last_start_at: '', next_start_at: '', schedule_type: 'time', schedule_value: '23:05'},
        // ];
        // this.setState({
        //     isLoaded: true,
        //     items: result
        // });
        // return;
        fetch("?list=1", {
            credentials: "same-origin"
        })
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result.items
                    });
                },
                // Note: it's important to handle errors here
                // instead of a catch() block so that we don't swallow
                // exceptions from actual bugs in components.
                (error) => {
                    // this.setState({
                    //     isLoaded: true,
                    //     error
                    // });
                }
            );
    }


    componentDidMount() {
        this.loadData();
        setInterval(this.loadData, 5000);
    }

    render() {
        const { error, isLoaded, items } = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return <div>Loading...</div>;
        } else {
            return (
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>#</TableCell>
                            <TableCell>Name</TableCell>
                            <TableCell>Server name</TableCell>
                            <TableCell>Status</TableCell>
                            <TableCell>Last start at</TableCell>
                            <TableCell>Next start at</TableCell>
                            <TableCell>Schedule type</TableCell>
                            <TableCell>Schedule value</TableCell>
                            <TableCell colSpan="2"> </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                    {items.map((row, key) => (
                        <TableRow key={key}>
                            <TableCell scope="row">{ row.id }</TableCell>
                            <TableCell>{ row.name }</TableCell>
                            <TableCell>{ row.server_name }</TableCell>
                            <TableCell>{ row.status }</TableCell>
                            <TableCell>{ row.last_start_at }</TableCell>
                            <TableCell>{ row.next_start_at }</TableCell>
                            <TableCell>{ row.schedule_type }</TableCell>
                            <TableCell>{ row.schedule_value }</TableCell>
                            <TableCell>
                                <Run item={row}/>
                            </TableCell>
                            <TableCell>
                                <Edit item={row}/>
                            </TableCell>
                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
            );
        }
    }
}



