import React from 'react';

import Button from 'material-ui/Button';

import Dialog, {
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
} from 'material-ui/Dialog';

import Radio, { RadioGroup } from 'material-ui/Radio';
import { FormLabel, FormControl, FormControlLabel, FormHelperText } from 'material-ui/Form';
import TextField from 'material-ui/TextField';

import Input, { InputLabel, InputAdornment } from 'material-ui/Input';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Cancel from '@material-ui/icons/Cancel';
import Save from '@material-ui/icons/Save';
import Create from '@material-ui/icons/Create';

export default class Edit extends React.Component {

    state = {
        open: false,
        scheduleType: this.props.item.schedule_type,
        scheduleValue: this.props.item.schedule_value,
    };

    handleClickOpen = () => {
        this.setState({ open: true });
    };

    handleClose = () => {
        this.setState({ open: false });
    };
    handleSave = () => {
        const name = encodeURIComponent(this.props.item.name);
        const type = encodeURIComponent(this.state.scheduleType);
        const value = encodeURIComponent(this.state.scheduleValue);

        fetch(`?name=${name}&fields[schedule_type]=${type}&fields[schedule_value]=${value}`, {
            credentials: "same-origin"
        }).then(
            (result) => {
                this.handleClose();
            },
            (error) => {

            }
        );
    };

    handleChangeValue = (event) => {
        this.setState({ scheduleValue: event.target.value });
    };
    handleChangeType = event => {
        this.setState({ scheduleType: event.target.value });
        console.log(this.input);
    };

    render() {
        var input = ('time' == this.state.scheduleType) ? (
            <FormControl component="fieldset" required>
                <TextField
                    id="time"
                    label="Time"
                    type="time"
                    defaultValue={this.props.item.schedule_type === this.state.scheduleType ? this.props.item.schedule_value : ''}
                    onChange={this.handleChangeValue}
                    InputLabelProps={{
                        shrink: true,
                    }}
                    inputProps={{
                        step: 60, // 1 min
                    }}
                />
                <FormHelperText>ex.: 23:05; 12:30; 00:00</FormHelperText>
            </FormControl>
        ) : (
            <FormControl component="fieldset" required>
                <TextField
                    InputProps={{
                        startAdornment: <InputAdornment position="start">Every:</InputAdornment>,
                    }}

                    defaultValue={this.props.item.schedule_type === this.state.scheduleType ? this.props.item.schedule_value : ''}
                    onChange={this.handleChangeValue}
                    label="Interval"
                />

                <FormHelperText>ex.: 10 minutes; 2 hours; 1 minute;</FormHelperText>
            </FormControl>
        );

        return (
            <div>
                <Button variant="raised" color="secondary" onClick={this.handleClickOpen}>Edit<Create /></Button>
                <Dialog
                    open={this.state.open}
                    onClose={this.handleClose}
                    aria-labelledby="form-dialog-title"
                >
                    <DialogTitle id="form-dialog-title">Update "{this.props.item.name}"</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Update command schedule
                        </DialogContentText>
                        <FormControl component="fieldset" required>
                            <RadioGroup
                                row
                                aria-label="type"
                                name="type"
                                value={this.state.scheduleType}
                                onChange={this.handleChangeType}
                            >
                                <FormControlLabel value="time" control={<Radio />} label="Time" />
                                <FormControlLabel value="interval" control={<Radio />} label="Interval" />
                            </RadioGroup>
                        </FormControl>
                        <br/>
                        {input}
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClose} variant="raised">
                            Cancel <Cancel />
                        </Button>
                        <Button onClick={this.handleSave} variant="raised" color="secondary">
                            Save <Save />
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}



